'use client';

import { useNewsletter } from '@/hooks/useNewsletter';
import { filterTodayTrends, filterTopTrends } from '@/utils/filterNewsletters';

import { styled } from 'styled-components';
import Text from '@/components/common/Text';
import Title from '@/components/common/Title';
import Button from '@/components/common/Button';
import FullWidthPanel from '@/components/common/FullWidthPanel';
import Card from '@/components/common/Card';
import CardSlider from '@/components/common/slider/CardSlider';
import { IoBookmarkOutline } from 'react-icons/io5';
import Loader from '@/components/common/Loader';
import { useCallback, useEffect, useRef, useState } from 'react';
import { Newsletter as INewsletter } from '@/models/newsletter.model';

const HomePage = () => {
	const { newsletters } = useNewsletter();

	const todayTrends = filterTodayTrends(newsletters);
	const topTrends = filterTopTrends(newsletters);

	const [currentPage, setCurrentPage] = useState(1); // 현재 페이지
	const [loadedNewsletters, setLoadedNewsletters] = useState<INewsletter[]>([]); // 로드된 데이터
	const [isLoading, setIsLoading] = useState(false); // 로딩 상태
	const observerRef = useRef<HTMLDivElement | null>(null);

	const ITEMS_PER_PAGE = 10; // 한 번에 로드할 항목 수

	// 현재 페이지 데이터 로드
	const fetchNewsletters = useCallback(
		(page: number) => {
			const startIndex = (page - 1) * ITEMS_PER_PAGE;
			const endIndex = page * ITEMS_PER_PAGE;
			return newsletters.slice(startIndex, endIndex);
		},
		[newsletters]
	);

	// 무한 스크롤을 처리하는 Intersection Observer
	useEffect(() => {
		const observer = new IntersectionObserver(
			([entry]) => {
				if (entry.isIntersecting && !isLoading) {
					setIsLoading(true);

					// 데이터 로드를 천천히 진행
					setTimeout(() => {
						setCurrentPage((prevPage) => prevPage + 1);
						setIsLoading(false);
					}, 1500); // 데이터 로드 지연 (1.5초)
				}
			},
			{ threshold: 1.0 } // 100% 노출 시 트리거
		);

		const currentObserverRef = observerRef.current;
		if (currentObserverRef) observer.observe(currentObserverRef);

		return () => {
			if (currentObserverRef) observer.unobserve(currentObserverRef);
		};
	}, [isLoading]);

	// 페이지 변경 시 데이터 추가 로드
	useEffect(() => {
		const newItems = fetchNewsletters(currentPage);
		setLoadedNewsletters((prev) => [...prev, ...newItems]);
	}, [currentPage, fetchNewsletters]);

	return (
		<StyledNewsletterPage>
			<HeroSection>
				<Title
					size="extraSmall"
					weight="semiBold"
					styles={{
						whiteSpace: 'nowrap',
					}}
				>
					오늘의 트렌드
				</Title>
				<hr />
				<CardSlider
					type="main"
					data={todayTrends.map((trend) => ({
						id: trend.id,
						image: trend.image,
						header: trend.category,
						main: {
							title: trend.title,
							description: trend.summary,
						},
						footer: (
							<>
								<div className="rounded-icon">
									<Button size="large">
										<IoBookmarkOutline />
									</Button>
								</div>
								|<Text color="subText">{trend.date}</Text>
							</>
						),
					}))}
				/>
			</HeroSection>

			<FullWidthPanel background="surface">
				<TrendSection>
					<div className="trend-header">
						<Title size="extraSmall" weight="semiBold">
							지금 가장 핫한 뉴스레터
						</Title>
					</div>
					<div className="trend-cards">
						{topTrends.map((trend, index) => (
							<Card
								key={index}
								data={{
									id: trend.id,
									image: trend.image,
									header: trend.category,
									main: {
										title: trend.title,
										description: trend.summary,
									},
									footer: (
										<>
											<Text color="subText">{trend.date}</Text>
											<div className="right">
												<div className="rounded-icon">
													<Button size="large">
														<IoBookmarkOutline />
													</Button>
													<Text color="subText">{trend.bookmarks}</Text>
												</div>
											</div>
										</>
									),
								}}
							/>
						))}
					</div>
				</TrendSection>
			</FullWidthPanel>

			<ListSection>
				<Title size="extraSmall" weight="semiBold">
					전체 뉴스레터
				</Title>
				<hr />
				<div className="newsletter-cards">
					{loadedNewsletters.map((newsletter, index) => (
						<Card
							key={`${newsletter.id}-${index}`}
							data={{
								id: newsletter.id,
								image: newsletter.image,
								header: newsletter.category,
								main: {
									title: newsletter.title,
									description: newsletter.summary,
								},
								footer: <Text color="subText">{newsletter.date}</Text>,
							}}
						/>
					))}
				</div>
				<div ref={observerRef} className="observer">
					{isLoading && <Loader />}
				</div>
			</ListSection>
		</StyledNewsletterPage>
	);
};

const StyledNewsletterPage = styled.div`
	width: 100%;
	gap: 2rem;

	hr {
		width: 100%;
		border: none;
		border-bottom: 1px solid ${({ theme }) => theme.color.border};
		margin: 0;
		padding: 0;
	}

	@media ${({ theme }) => theme.mediaQuery.tablet} {
		padding: 0 1rem;
	}
`;

const HeroSection = styled.section`
	width: 100%;
	height: fit-content;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: flex-start;
	gap: 1rem;
	padding: 3rem 0;

	@media ${({ theme }) => theme.mediaQuery.mobile} {
		flex-wrap: wrap;
	}
`;

const TrendSection = styled.section`
	width: ${({ theme }) => theme.layout.width.large};
	height: fit-content;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: flex-start;
	gap: 1rem;
	padding: 2rem 0;

	.trend-header {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: center;
		width: 100%;
		gap: 1rem;
	}

	.trend-cards {
		width: 100%;
		display: grid;
		grid-template-columns: repeat(4, 1fr);
		gap: 1rem;
	}
`;

const ListSection = styled.section`
	width: 100%;
	height: fit-content;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: flex-start;
	gap: 1rem;
	padding: 2rem 0;

	.newsletter-cards {
		width: 100%;
		display: grid;
		grid-template-columns: repeat(3, 1fr);
		gap: 1rem;
	}

	.observer {
		width: 100%;
		display: flex;
		justify-content: center;
		align-items: center;
		margin-top: 1rem;
	}
`;

export default HomePage;
